@import url("https://fonts.googleapis.com/css?family=Source+Sans+Pro:400,700");
html *,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
::selection {
  color: #222;
  background: #f9bf3b;
}
html {
  font-size: 100%;
  height: 100%;
}
body {
  font: 1rem/1.45 'Source Sans Pro', sans-serif;
  font-style: normal;
  height: 100%;
  background: #fbfaf8;
  color: #141414;
}
@media only screen and (min-width: 60em) {
  body {
    font-size: 1.4rem;
  }
}
ul,
ol {
  list-style: none;
}
a {
  text-decoration: none;
}
header,
main,
footer {
  display: block;
}
header {
  background: url("../img/bg.jpg") no-repeat #29242d;
  background-size: cover;
  color: #f5f3ee;
  padding: 2rem;
  overflow: hidden;
}
@media only screen and (min-width: 37.5em) {
  header {
    padding: 4rem;
  }
}
@media only screen and (min-width: 48em) {
  header {
    padding: 6rem;
  }
}
@media only screen and (min-width: 80em) {
  header {
    min-height: 100%;
    position: fixed;
    width: 40%;
    left: 0;
    top: 0;
    padding: 6rem;
  }
}
@media only screen and (min-width: 100em) {
  header {
    padding: 8rem;
  }
}
header h1 {
  text-indent: 200%;
  white-space: nowrap;
  overflow: hidden;
  width: 240px;
  height: 76.8px;
  margin: 0 auto;
  margin-bottom: 2rem;
  background: url("../img/kuvaify-logo.svg") no-repeat;
  background-size: contain;
}
@media only screen and (min-width: 48em) {
  header h1 {
    margin-bottom: 4rem;
  }
}
@media only screen and (min-width: 37.5em) {
  header h1 {
    width: 400px;
    height: 128px;
  }
}
@media only screen and (min-width: 80em) {
  header h1 {
    width: 320px;
    height: 102.4px;
  }
}
@media only screen and (min-width: 100em) {
  header h1 {
    width: 400px;
    height: 128px;
  }
}
header p {
  font-size: 1.2rem;
  text-align: center;
  max-width: 400px;
  margin: 0 auto 2rem;
}
@media only screen and (min-width: 48em) {
  header p {
    margin-bottom: 4rem;
  }
}
@media only screen and (min-width: 80em) {
  header p {
    font-size: 1.6rem;
  }
}
@media only screen and (min-width: 60em) {
  header p {
    font-size: 1.6rem;
  }
}
header ul {
  text-align: center;
  margin-bottom: 2rem;
}
header ul li {
  display: inline-block;
}
header ul a {
  display: block;
  position: relative;
  padding: 1.5rem 2rem;
  color: #fff;
  border-radius: 2px;
  overflow: hidden;
}
header ul a:hover:after {
  -webkit-transform: translateY(0%) skewY(0);
  -ms-transform: translateY(0%) skewY(0);
  transform: translateY(0%) skewY(0);
}
header ul a:after {
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  -webkit-transform: translateY(120%) skewY(-10deg);
  -ms-transform: translateY(120%) skewY(-10deg);
  transform: translateY(120%) skewY(-10deg);
  content: '';
  width: 100%;
  height: 100%;
  transition: transform 0.4s ease;
}
header ul li:nth-of-type(1) a {
  background: #74c88c;
  color: #245b33;
}
header ul li:nth-of-type(1) a:after {
  background: rgba(135,239,165,0.6);
}
header ul li:nth-of-type(2) a {
  background: #d85e46;
  color: #5e2014;
}
header ul li:nth-of-type(2) a:after {
  background: rgba(255,114,84,0.6);
}
header > a {
  text-indent: 200%;
  white-space: nowrap;
  overflow: hidden;
  display: block;
  margin: 0 auto;
  width: 90px;
  height: 30px;
  background: url("../img/github.svg") no-repeat;
  opacity: 0.2;
  transition: opacity 0.4s ease;
}
header > a:hover {
  opacity: 0.6;
}
@media only screen and (min-width: 80em) {
  main {
    float: right;
    width: 60%;
  }
}
main a {
  color: #38427a;
  transition: color 0.2s ease;
}
main a:hover,
main a:focus {
  color: #5766b2;
}
main h2 {
  font-weight: 900;
  font-size: 1.4rem;
  margin-bottom: 2rem;
}
@media only screen and (min-width: 80em) {
  main h2 {
    font-size: 2rem;
  }
}
@media only screen and (min-width: 60em) {
  main h2 {
    font-size: 2rem;
  }
}
main ol {
  background: #d1d5ea;
  padding: 2rem 0 0 2rem;
  margin: 0;
}
@media only screen and (min-width: 37.5em) {
  main ol {
    padding: 2rem 0 0 4rem;
  }
}
@media only screen and (min-width: 48em) {
  main ol {
    padding: 4rem 0 0 6rem;
  }
}
main ol h2 {
  color: #6972a0;
}
main ol li {
  counter-increment: step-counter;
  position: relative;
  margin-bottom: 2rem;
}
main ol li:nth-of-type(3) {
  margin-bottom: 0;
}
@media only screen and (max-width: 37.4375em) {
  main ol li {
    padding-left: 2rem;
  }
}
main ol li:before {
  content: counter(step-counter);
  display: block;
  position: absolute;
  width: 2rem;
  height: 2rem;
  left: -3rem;
  top: -0.25rem;
  border-radius: 2rem;
  background: #6972a0;
  color: #fff;
  text-align: center;
  line-height: 2;
}
@media only screen and (max-width: 37.4375em) {
  main ol li:before {
    left: -0.75rem;
  }
}
@media only screen and (min-width: 60em) {
  main ol li:before {
    top: 0;
    line-height: 1.4;
  }
}
main ol li p {
  margin-bottom: 2rem;
}
main strong,
main span,
main dt {
  color: #554166;
  font-weight: 400;
}
main pre {
  padding: 1rem;
  background: rgba(255,255,255,0.2);
  border-left: 2px solid rgba(87,102,178,0.4);
  white-space: pre-wrap;
  margin: 0;
}
main dl {
  padding: 2rem 2rem 0;
}
@media only screen and (min-width: 37.5em) {
  main dl {
    padding: 4rem 4rem 2rem;
  }
}
@media only screen and (min-width: 48em) {
  main dl {
    padding: 4rem 6rem;
  }
}
main dl h2 {
  color: #6972a0;
}
main dl pre {
  margin-top: 1rem;
}
main dt {
  font-size: 1.4rem;
  font-weight: 700;
}
@media only screen and (min-width: 80em) {
  main dt {
    font-size: 2rem;
  }
}
main dd {
  margin: 1rem 0 2rem;
}
main dd pre {
  background: #f7f5f1;
}
main #links {
  padding-left: 2rem;
}
@media only screen and (min-width: 37.5em) {
  main #links {
    padding-left: 4rem;
    padding-bottom: 2rem;
  }
}
@media only screen and (min-width: 48em) {
  main #links {
    padding-left: 6rem;
    padding-bottom: 4rem;
  }
}
main #links h2 {
  color: #6972a0;
}
main #links li {
  display: inline-block;
  margin-bottom: 2rem;
  margin-right: 2rem;
}
main #links li a img {
  display: block;
  width: 100px;
  height: 100px;
  border-radius: 100px;
  overflow: hidden;
  transition: transform 0.2s ease;
}
main #links li a:hover img {
  -webkit-transform: scale(1.1) rotate(-2deg);
  -ms-transform: scale(1.1) rotate(-2deg);
  transform: scale(1.1) rotate(-2deg);
}
